<template>
	<div class="books-detail">
		<header-back title="Book"></header-back>
		<div class="books-detail-content">
			<h3>{{ bookslist[index]['title'] }}</h3>
			<img :src="bookslist[index]['img']"/>
			<p>{{  bookslist[index]['sub2'] }}</p>
			<button @click="addMyCollection">收藏</button>
			<button @click="shareMyBooks">分享</button>
			<!-- JiaThis Button BEGIN -->
			<div class="jiathis_style_m"></div>
			<!-- JiaThis Button END -->
		</div>
	</div>
</template>

<script>
	import HeaderBack from '../common/header_back'
	import Store from 'store'
	export default{
		name:'books-detail',
		data(){
			return{
				localData:Store.get('booksData'),
				tipFlag:false,
				tip:''
			}
		},
		components:{
			HeaderBack
		},
		computed:{
			bookslist(){
				return this.localData.booksLists
			},
			index(){
				return this.localData.index
			}
		},
		mounted(){
			this.init(),
			this.goTop()
		},
		methods:{
			addMyCollection(){
				this.$store.commit('addMyCollection',{
					book:this.bookslist[this.index]
				})
			},
			shareMyBooks(){
				this.$store.commit('shareMyBooks',{
					book:this.bookslist[this.index]
				})
			},
			init:()=>{
		        let url = 'http://v3.jiathis.com/code/jiathis_m.js'
		        let script = document.createElement('script')
		        script.setAttribute('src', url)
		        document.getElementsByTagName('head')[0].appendChild(script)
		    },
		    goTop:function(){
				document.body.scrollTop = 0;
			}
		}
	}
</script>

<style scoped>
	.books-detail{
		width: 16rem;
	}
	.books-detail-content{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 16rem;
		position: absolute;
		left: 0;
		top: 3rem;
		box-sizing:border-box;	
	}
	.books-detail-content h3{
		font-size: 1.5rem;
		display: flex;
		justify-content: center;
		align-items: center;
		font-weight: bold;
		margin-bottom: 0.5rem;
	}
	.books-detail-content img{
		width: 10rem;
		/*margin-bottom: 1rem;*/
	}
	
	.books-detail-content  p{
		text-indent: 2em;
		width: 8.9rem;
		font-size: 0.7rem;
		line-height: 1rem;
		padding: 0 0.5rem;
		color: #999;
		border: 0.02rem solid #999;
		border-top: none;
	}
	.books-detail-content button{
		width: 3rem;
	}
</style>


