<template>
	<div>
		<logo-bar></logo-bar>
		<ul class="books-list">
			<li v-for="(item,index) in books" @click="getBookDetail(index)">
				<img :src="item.img">
				<div class="books-sub">
					<h3>{{ item.title }}</h3>
				</div>
				<span>{{ item.reading }}</span>
			</li>
		</ul>
		<NavBar></NavBar>
		<infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
	</div>
</template>

<script>
	import NavBar from '../common/navbar'
	import LogoBar from '../common/logoBar'
	import InfiniteLoading from 'vue-infinite-loading';
	import Vue from 'vue'
	import VueResource from 'vue-resource'
	import Store from 'store';

	Vue.use(VueResource);

	export default  {
		name:'information',
		data(){
			return{
				books:[]
			}
		},
		methods:{
			onInfinite() {
			    this.$http.jsonp('http://apis.juhe.cn/goodbook/query',{
			  		params:{
			  			key:'4c80bf0cb1c4b9d6bb789322253fd580',
			  			catalog_id:242,
			  			pn:this.books.length,
			  			rn:10,
			  			dtype:'json'
			  		}
			    }).then((res) => {
			        if (res.body.result.data.length) {
			        	if(!res.body.result.data)return;
			            this.books = this.books.concat(res.body.result.data);
			            this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
			            if(this.books.length / 20 === 10) {
			                this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
			            }
			        } else {
			          this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
			        }
			    });
			},
			getBookDetail(index){
				this.$router.push({name:'books-detail',params:{ id:index }});
				var booksData = {'index':index, 'booksLists': this.books};
				Store.set('booksData', booksData);
			}
		},
		components:{
			NavBar,
			LogoBar,
			InfiniteLoading
		}
	}
</script>

<style scoped>
	.books-list{
		width: 16rem;
		display: flex;
		flex-direction: column;
		position: absolute;
		left: 0;
		top: 3rem;
		padding:0 0.2rem;
		box-sizing:border-box;
	}
	.books-list li{
		display: flex;
		flex-direction: row; 
		align-items: center;
		border-bottom: 1px solid #000;
		margin-bottom: 0.21rem;
		font-size: 0.8rem;
		padding: 0.2rem 0;
		box-sizing:border-box;
	}
	.books-list li img {
		width: 4.3rem;
		vertical-align: bottom;
		flex: 1;
	}
	.books-sub{
		display: flex;
		align-items: center;
		justify-content: space-around;
		flex-direction: column;
		flex: 2;
	}
</style>


