<template>
	<div class="scroller-list">
	  <h3>图片福利</h3>
	  <div class="scroller-left">
	  	<div v-for="(item,index) in list" target="_blank" class="scroller-a" @click="getPhotoDetail(index)">
	    	<img :src="item.url"/>
	  	</div>
	  </div>
	  <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
	</div>
</template>

<script>
	import InfiniteLoading from 'vue-infinite-loading';
	import Vue from 'vue'
	import VueResource from 'vue-resource'
	import Store from 'store';

	Vue.use(VueResource);

	export default {
	  name:'scroller',
	  data() {
	    return {
	      list: []
	    };
	  },
	  mounted:function(){
	   },
	  methods: {
	    onInfinite() {
	      this.$http.get(`https://gank.io/api/data/福利/10/${this.list.length/10+1}`
	        ).then((res) => {
	          if (res.body.results.length) {
	            this.list = this.list.concat(res.body.results);
	            this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
	            if (this.list.length / 20 === 10) {
	              this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
	            }
	          } else {
	            this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
	          }
	      });
	    },
	    getPhotoDetail(index){
	    	this.$router.push({name:'scroller-detail',params:{ id:this.list[index]._id }});
	    	var photoData = {'index':index, 'photoLists': this.list};
	    	Store.set('photoData', photoData);
	    }
	  },
	  components: {
	    InfiniteLoading
	  }
	};
</script>

<style scoped>
	.scoller-list{
		width: 16rem;
		
	}
	.scroller-list h3{
		line-height: 3rem;
		font-weight: bold;
	}
	.scroller-left{
		display: flex;
		flex-wrap: wrap;
		width: 16rem;
		border-sizing:border-box;
		margin:0;
	}
	.scroller-left div{
		height: 5.18rem;
      	flex-grow:1;  
     	margin: 2px
	}
	.scroller-list img{
		height: 5.18rem;
      	min-width: 100%;     
      	max-width: 100%;
        object-fit: cover;   
        vertical-align: bottom;
	}
</style>


