<template>
	<div class="goback">
		<span @click="goBack">上一个</span>
		<div class="my-title" v-text="title">
		</div>
	</div>
</template>

<script>
	export default {
		name:'heaer-back',
		props:['title'],
		methods:{
			goBack(){
				history.go(-1)
			}
		}
	}
</script>

<style scoped>
	.goback{
		width: 16rem;
		position: fixed;
		left: 0;
		top: 0;
		background-color: #13CE66;
		height: 3rem;
		z-index: 100000;
	}
	.my-title{
		display: flex;
		align-items: center;
		justify-content: center;
		height:100%;
	}
	.goback span{
		position: absolute;
		left: 0.1rem;
		top: 50%;
		transform: translate(0,-50%);
		z-index: 1000;
	}
</style>



