<template>
	<div id="regist">
		<div class="username">
			<div>
				<span>用户名</span>
			</div>
			<div>
				<input type="text"
					placeholder="请输入账号" 
					v-model="username"
					ref="userInput"
					v-on:blur="userCheck"
				>
				<p v-text="userErr"></p>
			</div>
		</div>
		<div class="password">
			<div>
				<span>密码</span>
			</div>
			<div>
				<input type="password"
					placeholder="请输入密码" 
					v-model="password"
					ref="passInput"
					v-on:blur="passCheck"
				>
				<p v-text="passErr"></p>
			</div>
		</div>
		<div class="account">
			<div>
				<span>账号</span>
			</div>
			<div>
				<input type="text"
					placeholder="请输入账号名" 
					v-model="account"
					ref="accInput"
					v-on:blur="accCheck"
				>
				<p v-text="accErr"></p>
			</div>
		</div>
		<div class="tapButton">
			<v-touch  v-on:tap="regist">注册</v-touch>	
		</div>
		<div>
			<h3 v-text="errMsg"></h3>
		</div>
		<div>
			<span>跳转</span>
			<v-touch  v-on:tap="login">登录页面</v-touch>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueResource from 'vue-resource'
	import Store from 'store';

	Vue.use(VueResource);

	export default {
		name:'regist',
		data(){
			return {
				username:'',
				password:'',
				account:'',
				userErr:'',
				passErr:'',
				accErr:'',
				usernameTig:false,
				passwordTig:false,
				accdTig:false,
				errMsg:''
			}
		},
		mounted(){
			
		},
		methods:{
			regist(){
				let { username,password,usernameTig,passwordTig,account } = this;

				if(usernameTig&&passwordTig){
					this.$http.get('http://localhost:9999/sign',{
						params:{
							accountNumber:username,
							password,
							username:account
						}
					}).then(res=>{
						this.errMsg = res.body.errMsg
						if(res.body.errCode === 0){
							setTimeout(()=>{
								this.$router.push('/regist')
							},2000)
						}
					})
				}
			},
			userCheck(){
				if(!this.username){
					this.userErr = '用户名不能为空';
					this.usernameTig = false;
				}else if(!/^[a-zA-Z0-9_]{3,16}$/.test(this.username)){
					this.userErr = '用户名格式不对';
					this.usernameTig = false
				}else{
					this.userErr = '';
					this.usernameTig = true
				}
			},
			passCheck(){
				if(!this.password){
					this.passErr = '密码不能为空';
					this.passwordTig = false;
				}else if(!/^[a-zA-Z0-9_]{3,16}$/.test(this.password)){
					this.passErr = '密码名格式不对';
					this.passwordTig = false
				}else{
					this.passErr = '';
					this.passwordTig = true
				}
			},
			accCheck(){
				if(!this.account){
					this.accErr = '账号名不能为空';
					this.accTig = false;
				}else if(!/^[a-zA-Z0-9_]{3,16}$/.test(this.account)){
					this.accErr = '账号名格式不对';
					this.paccTig = false
				}else{
					this.accErr = '';
					this.accTig = true
				}
			},
			login(){
				this.$router.push('/login')
			}
		}
	}
</script>

<style scoped>
	.tapButton{
		margin-top: 20px;
	}
</style>


