<template>
	<div class="navigator-bottom">
	<md-bottom-bar class="md-warn" md-shift>
	    <md-bottom-bar-item md-icon="ondemand_video" md-active>
	    	<v-touch v-on:tap="gotoHomePage">首页</v-touch>
	    </md-bottom-bar-item>
	    <md-bottom-bar-item md-icon="books">
	    	<v-touch v-on:tap="gotoInformation">图书</v-touch>
	    </md-bottom-bar-item>
	    <md-bottom-bar-item md-icon="music_note">
	    	<v-touch v-on:tap="gotoTribune">资讯</v-touch>
	    </md-bottom-bar-item>
	    <md-bottom-bar-item md-icon="photo">
	    	<v-touch v-on:tap="gotoMyPage">个人主页</v-touch>
	    </md-bottom-bar-item>
	</md-bottom-bar>
  	</div>
</template>

<script>
	export default {
		name:'nav-bar',
		methods:{
			gotoHomePage(){
				this.$router.push('/homepage')
			},
			gotoInformation(){
				this.$router.push('/information')
			},
			gotoTribune(){
				this.$router.push('/tribune')
			},
			gotoMyPage(){
				this.$router.push('/mypage')
			},
		}
	}	
</script>

<style scoped>
	.navigator-bottom{
		width: 100%;
		/*display: flex;*/
		background: rgba(0,0,0,0);
		position: fixed;
		left: 0;
		bottom: 0;
	}
	.navigator-bottom a{
		/*flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 1rem;
		color: #fff;
		height: 3rem;
		line-height: 3rem;
		background: #13CE66;*/
	}
</style>


