<template>
	<div class="mypage">
		<logo-bar></logo-bar>
		<div class="user">
			<div class="head-portrait">
			</div>
			<div>
				<h3 v-text="users"></h3>
				<v-touch v-on:tap="tapHandler">
					<span>个人信息</span>
				</v-touch>
				<span>一个简单的前端程序员</span>
			</div>
		</div>
		<ul class="function">
			<li v-for="(item,index) in lists" @click="clickHandler(index)">
				<span>{{ item }}</span>
				<i>></i>
			</li>
		</ul>
		<v-touch v-on:tap="tapOffHandler">
			<div class="log-off">
				<span>退出账号</span>
			</div>
		</v-touch>
		<NavBar></NavBar>
	</div>
</template>

<script>
	import NavBar from '../common/navbar'
	import LogoBar from '../common/logoBar'
	import Store from 'store'

	export default  {
		name:'mypage',
		data(){
			return{
				localUserData:Store.get('userMsg'),
				localCollectionData:Store.get('booksData'),
				lists:['我的收藏','消息通知','意见反馈','设置'],
				items:['mycollection','messageReminding','suggestions','install']
			}
		},
		computed:{
			photolist(){
				return this.localCollectionData.booksData
			},
			index(){
				return this.localCollectionData.index
			},
			users(){
				return this.localUserData.username
			}
		},
		components:{
			NavBar,
			LogoBar
		},
		methods:{
			clickHandler(index){
				this.$router.push(`/${this.items[index]}`)
			},
			tapHandler(){
				this.$router.push('/mypersonpage')
			},
			tapOffHandler(){
				Store.remove('userMsg');
				this.$router.push('/login')
			}
		}
	}
</script>

<style scoped>
	.mypage{
		width: 16rem;
	}
	.user{
		position: absolute;
		left: 0;
		top: 3rem;
		width: 16rem;
		background-color: rgba(131,175,155,1);
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		text-align: center;
		line-height: 1.5rem;
	}
	.head-portrait{
		background: #fff;
		width: 4rem;
		height: 4rem;
		border-radius: 50%;
		background:url(../../assets/logo.png) no-repeat 0 0.3rem #fff;
		background-size: 100% 100%;
	}
	.head-portrait img{
		width: 100%;
		height: 100%;
	}
	.function{
		width: 16rem;
		position: absolute;
		left: 0;
		top: 12rem;
		line-height: 2rem;
		padding: 0 0.5rem;
		box-sizing:border-box;
	}
	.function li{
		width: 100%;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid #ccc;
	}
	.log-off{
		position: absolute;
		left: 50%;
		top: 21.5rem;
		transform: translate(-50%,0);
		width: 10rem;
		height: 3rem;
		background-color:rgba(254,67,101,1);
		text-align: center;
		line-height: 3rem;
		color: #fff;
		border-radius: 0.2rem;
	}
</style>w


