import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '../components/Homepage/Homepage'
import Information from '../components/Information/Information'
import Tribune from '../components/Tribune/Tribune'
import Mypage from '../components/Mypage/Mypage'
import ScrollerDetail from '../components/Homepage/scroller_detail'
import BooksDetail from '../components/Information/Information_detail'
import Login from '../views/login/index'
import Regist from '../views/regist/index'

Vue.use(Router)

export default new Router({
  routes: [
  	{path:'/homepage',component: Homepage},
  	{path:'/information',component:Information},
  	{path:'/tribune',component:Tribune},
  	{path:'/mypage',component:Mypage},
  	{path:'/homepage/scroller-detail/:id',component:ScrollerDetail,name: 'scroller-detail'},
  	{path:'/',redirect:'/homepage'},
  	{path:'/information/books-detail/:id',component:BooksDetail,name: 'books-detail'},
  	{path:'/login',component:Login,name: 'login'},
  	{path:'/regist',component:Regist,name: 'regist'},	
  ]
})



