<template>
	<div id="login">
		<div class="username">
			<div>
				<span>用户名</span>
			</div>
			<div>
				<input type="text"
					placeholder="请输入账号" 
					v-model="username"
					ref="userInput"
					v-on:blur="userCheck"
				>
				<p v-text="userErr"></p>
			</div>
		</div>
		<div class="password">
			<div>
				<span>密码</span>
			</div>
			<div>
				<input type="password"
					placeholder="请输入密码" 
					v-model="password"
					ref="passInput"
					v-on:blur="passCheck"
				>
				<p v-text="passErr"></p>
			</div>
		</div>
		<div class="tapButton">
			<v-touch  v-on:tap="login">登录</v-touch>
			<v-touch  v-on:tap="regist">注册</v-touch>	
		</div>
		<div>
			<h3 v-text="errMsg"></h3>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueResource from 'vue-resource'
	import Store from 'store';

	Vue.use(VueResource);

	export default {
		name:'login',
		data(){
			return {
				username:'',
				password:'',
				userErr:'',
				passErr:'',
				usernameTig:true,
				passwordTig:true,
				errMsg:''
			}
		},
		mounted(){
			this.check();
		},
		methods:{
			login(){
				let { username,password,usernameTig,passwordTig } = this;

				if(usernameTig&&passwordTig){
					this.$http.get('http://localhost:9999/login',{
						params:{
							accountNumber:username,
							password
						}
					}).then(res=>{
						console.log(res.body)
						this.errMsg = res.body[0].errMsg
						if(res.body[0].errCode === 7){
							this.errMsg = '登录成功';
							this.$router.push('/homepage')
							let userMsg = { 'username':username,'password':password }
							Store.set('userMsg',userMsg)
						}
					})
				}
			},
			regist(){
				this.$router.push({name:'regist'})
			},
			userCheck(){
				if(!this.username){
					this.userErr = '用户名不能为空';
					this.usernameTig = false;
				}else if(!/^[a-zA-Z0-9_]{3,16}$/.test(this.username)){
					this.userErr = '用户名格式不对';
					this.usernameTig = false
				}else{
					this.userErr = '';
					this.usernameTig = true
				}
			},
			passCheck(){
				if(!this.password){
					this.passErr = '密码不能为空';
					this.passwordTig = false;
				}else if(!/^[a-zA-Z0-9_]{3,16}$/.test(this.password)){
					this.passErr = '密码名格式不对';
					this.passwordTig = false
				}else{
					this.passErr = '';
					this.passwordTig = true
				}
			},
			check(){
				
				if(Store.get('userMsg')){
					console.log(111)
					this.$router.push('/homepage')
				}
			}
		}
	}
</script>

<style scoped>
	.tapButton{
		margin-top: 20px;
	}
</style>


