<template>
	<div class="logoBar">
		<md-toolbar class="md-warn logo-button" >
		  <md-button class=" " @click.native="toggleLeftSidenav">
		    <md-icon>menu</md-icon>
		  </md-button>
		  <h2 class="md-title" style="flex: 1" v-text="">My Vue</h2>
		  <md-button class="md-icon-button">
		    <md-icon>message</md-icon>
		  </md-button>
		</md-toolbar>

		<md-sidenav class="md-left" ref="leftSidenav" @open="open('Left')" @close="close('Left')">
		  <md-toolbar class="md-large">
		    <div class="md-toolbar-container">
		      <h3 class="md-title">侧边导航内容</h3>
		    </div>
		  </md-toolbar>

		  <p>人心就像一个容器，装的快乐多了，烦恼自然就少；装的简单多了，纠结自然就少；装的满足多了，痛苦自然就少；装的理解多了，矛盾自然就少；装的宽容多了，仇恨自然就少。胸襟决定器量，境界决定高下。活着就是一种修行，修行就是修心。修得一颗平常心，无时不是快乐；修得一颗满足心，无处不是幸福。</p>
		</md-sidenav>
	</div>	
</template>

<script>
	import Store from 'store';
	
	export default {
		name:'logo-bar',
		methods:{
			toggleLeftSidenav() {
		      this.$refs.leftSidenav.toggle();
		      console.log(111)
		    },
		    open(ref) {
		      console.log('Opened: ' + ref);
		    },
		    close(ref) {
		      console.log('关闭d: ' + ref);
		    }
		}
	}
</script>

<style scoped>
	.logoBar{
		width: 16rem;
		height: 3rem;
		position: fixed;
		left: 0;
		top: 0;
		z-index:999999999;
		/*z-index: 9999999;
		font-size: 1rem;*/
		/*background-color: #13CE66;
		display: flex;*/
	}
	.logo-button{
		height: 3rem;
	}
	.logo-button h2.md-title{
		font-size: 1rem;
	}
</style>


