<template>
	<div id="scroller-detail">
		<transition name="slideT">
		    <div class="login-tip" v-show="tipFlag" v-text="tip"></div>
		</transition>
		<header-back title="photo"></header-back>
		<v-touch v-on:swipeleft="onSwiperPre" v-on:swiperight="onSwiperNext" class="photo-item">
			<img :src="photolist[index]['url']">
		</v-touch>
	</div>
</template>

<script>
	import HeaderBack from '../common/header_back'
	import Store from 'store'
	export default{
		name:'scroller-detail',
		data(){
			return{
				localData:Store.get('photoData'),
				tipFlag:false,
				tip:''
			}
		},
		components:{
			HeaderBack
		},
		computed:{
			photolist(){
				return this.localData.photoLists
			},
			index(){
				return this.localData.index
			}
		},
		methods:{
			onSwiperNext(){
				if(this.localData.index===this.photolist.length-1){
					this.tip="这是最后一张图";

			        this.tipFlag = true;

			        setTimeout(()=>{

			          this.tipFlag = false;

			        },2000)

			        return;
				}
				this.localData.index++;
			},
			onSwiperPre(){
				if(this.localData.index===0){
					this.tip="这是第一张图";

			        this.tipFlag = true;

			        setTimeout(()=>{

			          this.tipFlag = false;

			        },2000)

			        return;
				}
				this.localData.index--;
			}
		}
	}
</script>

<style scoped>
	img{
		width: 16rem;
		position: absolute;
		left: 0;
		top: 5rem;
		z-index: 100000;
	}
	.login-tip{
	  position: fixed;
	  bottom: 0;
	  left: 0;
	  width: 100%;
	  min-height: 64px;
	  line-height: 64px;
	  text-align: center;
	  background-color: #3f51b5;
	  color: #fff;
	}
	.photo-item{
		width: 16rem;
	}
	.slideT-enter-active, .slideT-leave-active {
	  transition: all .5s
	}
	.slideT-enter-active{
	  transform: translateY(0);
	}
	.slideT-enter, .slideT-leave-active {
	  transform: translateY(100%);
	}
</style>


