<template>
	<div class="homepage">
		<logo-bar></logo-bar>
		<banner class="banner"></banner>
		<scroller class="scroller"></scroller>
		<NavBar></NavBar>
	</div>
</template>

<script>
	import Banner from './banner'
	import Scroller from './scroller'
	import NavBar from '../common/navbar'
	import LogoBar from '../common/logoBar'


	export default  {
		name:'home-page',
		components:{
			Banner,
			Scroller,
			NavBar,
			LogoBar
		}
	}
</script>

<style scoped>
	.homepage{
		position: absolute;
		left: 0;
		top: 3rem;
		width: 100%;
		display: flex;
		flex-direction: column;
	}
	.banner{
		flex: 1;

	}
	.scroller{
		flex: 3;
		width: 16rem;
	}
</style>


