<template>
	<div class="swiper-container">
		<div class="swiper-wrapper">
			<div v-for="(item,index) in bannerList"  class="swiper-slide" :style="{ backgroundImage: 'url(' + item + ')' }">
			</div>
		</div>
		<div class="swiper-pagination swiper-pagination-bullet"></div>
	</div>
</template>

<script>
	import bannerOne from '../../assets/banner2.jpg';
	import bannerTwo from '../../assets/banner1.jpg';
	import Swiper from 'swiper';
    import 'swiper/dist/css/swiper.min.css';
	export default {
		name:'banner',
		data(){
			return {
				bannerList:[bannerOne,bannerTwo],
			}
		},
		mounted() {
            var swiper = new Swiper('.swiper-container', {
                pagination: '.swiper-pagination',
                paginationClickable: true,
                loop: true,
                speed: 600,
                autoplay: 1000,
                onTouchEnd: function() {
                    swiper.startAutoplay()
                }
            });
        }
	}
</script>

<style scoped>
	.swiper-container {
        width: 16rem;
        height: 7.23rem;
        margin:0;
        padding: 0;
    }
    .swiper-wrapper {
        width: 100%;
        height: 7.23rem;
    }
    .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 10rem;
        background-size: 100% 100%;
    }
    .swiper-pagination-bullet {
        width:0.833rem;
        height: 0.833rem;
        display: inline-block;
        background: #7c5e53;
    }
 
</style>


