<template>
	<div>
		<logo-bar></logo-bar>
		<div>
			<div class="news-title">
				<v-touch v-for="(item,index) in lists" v-on:tap="tapHandler(index)" :key="index">
					<span :class="{active:currentIndex===index}">{{ item }}</span>
				</v-touch>
			</div>
			<ul class="news-content">
				<transition v-for="(item,index) in news " :key="index" name="fade">
					<li>
						<a :href="item.docurl">
							<h3>{{ item.title }}</h3>
						</a>
						<img :src="item.imgurl">
						<span>{{ item.time }}</span>	
					</li>
				</transition>
			</ul>
		</div>
		<infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
		<NavBar></NavBar>
	</div>
</template>

<script>
	import NavBar from '../common/navbar'
	import LogoBar from '../common/logoBar'
	import InfiniteLoading from 'vue-infinite-loading';
	import Vue from 'vue'
	import VueResource from 'vue-resource'
	import Store from 'store';

	export default  {
		name:'tribune',
		data(){
			return{
				lists:['军事','体育','科技','娱乐','财经'],
				items:['war','sport','tech','ent','money'],
				news:[],
				type:'war',
				currentIndex:''
			}
		},
		methods:{
			tapHandler(index){
				this.type = this.items[index];
				this.news = [];
				this.currentIndex = index;
				this.iNow = index;
				this.$nextTick(() => {
			       this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
			     });
				this.goTop();
			},
			onInfinite() {
			    this.$http.get('http://wangyi.butterfly.mopaasapp.com/news/api',{
			  		params:{
			  			type:this.type,
			  			page:this.news.length/10+1,
			  			limit:10
			  		}
			    }).then((res) => {
			        if (res.body.list.length) {
			            this.news = this.news.concat(res.body.list);
			            this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
			            if(this.news.length / 20 === 3) {
			                this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
			            }
			        } else {
			          this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
			        }
			    });
			},
			goTop(){
				document.body.scrollTop = 0;	
			}
		},
		components:{
			NavBar,
			LogoBar,
			InfiniteLoading
		}
	}
</script>

<style scoped>
	.news-title{
		width: 16rem;
		display: flex;
		justify-content: space-around;
		align-items: center;
		background-color: #ccc;
		position: fixed;
		left: 0;
		top: 3rem;
		z-index: 100000;
	}
	.news-title span{
		display: block;
		box-sizing:border-box;
		line-height: 3rem;
	}
	.news-title span.active{
		color: red;
	}
	.news-content{
		width: 16rem;
		position: absolute;
		left: 0;
		top: 6rem;
	}
	.news-content li{
		padding-bottom: 0.2rem;
		border-bottom: 0.02rem dashed #000;
	}
	.news-content h3{
		margin-top: 0.5rem;
		font-size: 1rem;
		font-weight: bold;
		color: #000;
		line-height: 1.5rem;
		width: 16rem;
	}
	.news-content img{
		width: 100%;
	}
	.news-content span{
		display: block;
		line-height: 2rem;
		font-size: 0.8rem;
		color: #333;
	}
	/*进入完成时的状态和离开时的状态*/
	.fade-enter-active,.fade-leave-active{
		transition: opacity .7s;
	}
	/*进入的时候和离开完成时候的状态*/
	.fade-enter,.fade-leave-to{
		opacity: 0;
	}
</style>


